<template>
  <v-card class="mx-auto">
    <v-card-title>
      <v-row justify="space-between">
        <v-col cols="12" sm="4">
          Логи
        </v-col>
        <v-col cols="12" sm="4">
          <v-select
            label="Категория" 
            :items="groups"
            v-model="group_type"
          ></v-select>
        </v-col>
        <v-col cols="12" sm="4">
          <v-text-field
            v-model="search"
            append-icon="mdi-magnify"
            label="Поиск"
          ></v-text-field>
        </v-col>
      </v-row>
    </v-card-title>
    <v-data-table
      :headers="headers"
      :items="filteredItems"
      :search="search"
    >
      <template v-slot:item.userId="props">
        <v-edit-dialog
          :return-value.sync="props.item.userId"
          large
          persistent
          @save="save"
          @cancel="cancel"
          @open="open"
          @close="close"
        >
          <span>{{ props.item.userId }}</span>
          <template v-slot:input>
            <v-text-field
              v-model="props.item.userId"
              label="Edit"
              single-line
              counter
              autofocus
            ></v-text-field>
          </template>
        </v-edit-dialog>
      </template>
      <template v-slot:item.userName="props">
        <v-edit-dialog
          :return-value.sync="props.item.userName"
          large
          persistent
          @save="save"
          @cancel="cancel"
          @open="open"
          @close="close"
        >
          <span>{{ props.item.userName }}</span>
          <template v-slot:input>
            <v-text-field
              v-model="props.item.userName"
              label="Edit"
              single-line
              counter
              autofocus
            ></v-text-field>
          </template>
        </v-edit-dialog>
      </template>
      <template v-slot:item.userRole="props">
        <v-edit-dialog
          :return-value.sync="props.item.userRole"
          large
          persistent
          @save="save"
          @cancel="cancel"
          @open="open"
          @close="close"
        >
          <span>{{ props.item.userRole }}</span>
          <template v-slot:input>
            <v-text-field
              v-model="props.item.userRole"
              label="Edit"
              single-line
              counter
              autofocus
            ></v-text-field>
          </template>
        </v-edit-dialog>
      </template>
      <template v-slot:item.date="props">
        <v-edit-dialog
          :return-value.sync="props.item.date"
          large
          persistent
          @save="save"
          @cancel="cancel"
          @open="open"
          @close="close"
        >
          <span>{{ props.item.date }}</span>
          <template v-slot:input>
            <v-text-field
              v-model="props.item.date"
              label="Edit"
              single-line
              counter
              autofocus
            ></v-text-field>
          </template>
        </v-edit-dialog>
      </template>
      <template v-slot:item.loginMethod="props">
        <v-edit-dialog
          :return-value.sync="props.item.loginMethod"
          large
          persistent
          @save="save"
          @cancel="cancel"
          @open="open"
          @close="close"
        >
          <span>{{ props.item.loginMethod }}</span>
          <template v-slot:input>
            <v-text-field
              v-model="props.item.loginMethod"
              label="Edit"
              single-line
              counter
              autofocus
            ></v-text-field>
          </template>
        </v-edit-dialog>
      </template>
      <template v-slot:item.action>
        <v-btn icon>
          <v-icon small>
            mdi-information-outline 
          </v-icon>
        </v-btn>
      </template> 
    </v-data-table>

    <v-snackbar 
      v-model="snack" 
      :timeout="3000" 
      :color="snackColor"
    >
      {{ snackText }}
      <v-btn 
        text 
        @click="snack = false"
      >
        Close
      </v-btn>
    </v-snackbar>
  </v-card>
</template>

<script>
export default {
  data: () => ({
    snack: false,
    snackColor: '',
    snackText: '',
    groups: ['All', 'Admin', 'Moderator', 'User'],
    group_type: null,
    search: '',
    headers: [
      { text: 'ID пользователя', align: 'left', value: 'userId' },
      { text: 'Имя пользователя', value: 'userName' },
      { text: 'Роль', value: 'userRole' },
      { text: 'Дата', value: 'date' },
      { text: 'Способ входа', value: 'loginMethod' },
      { text: 'Действия', value: 'action', sortable: false }
    ],
    data: [
      { 
        userId: 21,
        userName: 'Саша',
        userRole: 'moderator',
        date: new Date().toISOString().substr(0, 10),
        loginMethod: 'vkontakte'
      },
      { 
        userId: 22,
        userName: 'Ваня',
        userRole: 'user',
        date: new Date().toISOString().substr(0, 10),
        loginMethod: 'local'
      },
      { 
        userId: 30,
        userName: 'Катя',
        userRole: 'user',
        date: new Date().toISOString().substr(0, 10),
        loginMethod: 'vkontakte'
      },
      { 
        userId: 4,
        userName: 'Даша',
        userRole: 'user',
        date: new Date().toISOString().substr(0, 10),
        loginMethod: 'local'
      },
      { 
        userId: 51,
        userName: 'Леша',
        userRole: 'user',
        date: new Date().toISOString().substr(0, 10),
        loginMethod: 'vkontakte'
      },
      { 
        userId: 6,
        userName: 'Андрей',
        userRole: 'moderator',
        date: new Date().toISOString().substr(0, 10),
        loginMethod: 'vkontakte'
      },
      { 
        userId: 7,
        userName: 'Алекс',
        userRole: 'user',
        date: new Date().toISOString().substr(0, 10),
        loginMethod: 'vkontakte'
      }        
    ]
  }),
  computed: {
    filteredItems() {
      return this.data.filter((item) => {
        return (this.group_type == 'All') ? item : !this.group_type || (item.userRole.toLowerCase() === this.group_type.toLowerCase());
      });
    }
  },
  methods: {
    save() {
      this.snack = true;
      this.snackColor = 'success';
      this.snackText = 'Данные обновлены';
    },
    cancel() {
      this.snack = true;
      this.snackColor = 'error';
      this.snackText = 'Отмена';
    },
    open() {
      this.snack = true;
      this.snackColor = 'info';
      this.snackText = 'Редактирование';
    },
    close() {
      console.log('Dialog closed');
    }
  }
}
</script>
